.notes{
    padding-top: 4rem;
}
.note-preview{
    width: 100%;
    margin: 1rem 0;
    border-radius: 10px;
    padding: 1rem;
}
.note-title{
    font-size: 1.25rem;
}
.note-body{
    font-size: 1rem;
    word-wrap: break-word;
    padding-bottom: 0.5rem;
}
.note-date{
    font-size: 0.7rem;
}