@import url('https://fonts.googleapis.com/css2?family=Nunito:wght@400;600&display=swap');

*, *::before, *::after{
   box-sizing: border-box;
  }
  
*{
   margin: 0;
 }

body{
	font-family:'Nunito', sans-serif;
}

.home{
	margin-top: 2rem;
	background: #fdfdfd;
}
.container{
   padding: 0 0.5rem;
}

.header{
	padding: 1rem;
	padding-bottom: 0.5rem;
    background: white;
	position: fixed;
	top: 0;
	z-index: 1;
	width: 100%;
}
.header > h1{
	padding-left: 0.25rem;
	padding-bottom: 0.5rem;
	font-size: 2rem;
}
.search-bar{
	font-size: 1.25rem;
	font-weight: lighter;
	background: #E5E5E5;
	border: none;
	outline: 0;
	border-radius: 15px;
	width: 100%;
	padding: 0.25rem 0;
	padding-left: 1.25rem;
}
